article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;-moz-box-sizing:content-box;-webkit-box-sizing:content-box;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

html {
  font-size: 125%;
}

body {
  font-family: 'Poppins', cursive;
  color: #112d4e;
  background-color: #f9f7f7;
}

ul {
  list-style: none;
}

h1, h2, h3, p, ul {
  padding: 0;
  margin: 0;
}

img {
  width: 100%;
  display: block;
}

input, select {
  margin: 20px;
  font-size: 0.8rem;
  width: 50%;
  padding-left: 10px;
  border-radius: 10px;
  border: solid 1px #112d4e;
  
}

button {
  background-color: transparent;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  margin: 10px;
  color: inherit;
  transition: all 0.5s;
}


header {
  
  /* background-color: rgb(20, 26, 119); */
  background-color: #dbe2ef;
  margin-bottom: 40px;
  padding: 30px 0;
  min-height: 7vh;
  color: white;
  border-bottom: solid 5px #112d4e;
  
}

.headerMain {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}

nav {
  width: 100%;
  display: flex;
  color: #112d4e;
  justify-content: space-around;
}

h1 {
  font-size: 2rem;
  position: relative;
  color: #ff4343;
  z-index: 1;
  text-decoration: #112d4e underline;
  background-color: #f9f7f7;
  /* background-image: linear-gradient(to bottom right, #f9f7f7 , #112d4e ); */
  border-radius: 20px;
  padding: 10px 25px;
  

  font-family: 'Berkshire Swash', cursive;

    


}

h1:after{
  content: "";
  position: absolute;
  border-style: solid;
  border-color: #f9f7f7 transparent transparent transparent;
  border-width: 18px 18px 0px 18px;
  right: 25px;
  top: 98%;
  bottom: -20px;
  z-index: -1;
}

main {
  background-color: #f9f7f7;
  background-image: linear-gradient(to right top, #112d4e 50%, #f9f7f7 50% );
  /* color: white; */
  min-height: 88vh;
  padding-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;

}

h2 {
  text-align: center;
  padding-bottom: 30px;
}

h3 {
  padding: 20px;
  width: 60%;
  text-align: center;
}

span {
  color: #ff4343;
}

.wrapper {
  max-width: 1400px;
  width: 80%;
  margin:0 auto;
}

.App-link {
  color: #09d3ac;
}

/* Auth Options */

.options {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;
  
}

.optionsButtons {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.optionsButtons button {
  background-color: transparent;
  color: #112d4e;
  border: none;
  text-decoration: underline #ff4343;
  
  
}

.optionsButtons .inactive {
  opacity: 0.6;
  text-decoration: none;
  
}

/* Sign in page */

.signIn, .signUp, .guestSignIn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-basis: 60%;
  text-align: center;
  min-height: 75vh;
  background-color: #dbe2ef;
  color: white;
  /* padding-bottom: 50px; */
  border: solid 3px #112d4e;
  border-radius: 20px;
  color: #112d4e;
  animation: appear 0.5s both 1;
  box-shadow: 0px 0px 20px #112d4e;
  padding: 20px;
}

.signIn form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* width: 100%; */
}

/* Sign up page */

.signUp{
  
}

/* Guest signin page */

.guestSignIn {
  

}

.guestSignIn p {
  width: 80%;
}

.recoverPassword {
  animation: appear both 0.5s 1;
}

/* ChatPage */

.chatPageContainer {
  display: flex;
  width: 100%;
  align-items: flex-start;
  animation: appear 0.5s both 1;
}

.listOfFriends {
  display: flex;
  flex-direction: column;
  width:25%;
  padding: 20px;
  background-color: #dbe2ef;
  height: 80%;
  border: solid 5px #112d4e;
  align-items: center;
  color: #112d4e;
  border-radius: 20px;
  box-shadow: 0px 0px 5px #dbe2ef;
  transition: all 0.5s;
}

.addFriendBar {
  padding-bottom: 20px;
  text-align: center;
  
  
}

.addFriendBar input {
  width: 80%;
}



.friendButton {
  
  display: flex;
  align-items: center;
  border: solid black 3px;
  padding: 10px;
  background-color: white;
  border-radius: 20px;
  color: #112d4e;

}

.friendButton button {
  padding: 0;
  margin: 0;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.friendButton img {
  
  width: 50%;
}

.deleteButton {
  width:100%;
  font-size: 2rem;
  color: #ff4343;
  padding: 0px;
}

.messagesAndTextContainer {
  width:100%;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  
  transition: all 0.5s;
}

.messagesAndTextContainer h2 {
  
  color: #112d4e;
}

.recentMessagesDiv {
  width: 60%;
  border-radius: 20px;
  box-shadow: 0px 0px 5px #dbe2ef;
  
}

.chatHeader {
  display: flex;
  justify-content: start;
  align-items: center;
  background-color: #3f72af;
  padding: 10px;
  border-radius: 15px 15px 0 0;
  border: #112d4e solid 5px;

}

.chatHeader h2 {
  padding: 0;
  padding-left: 10px;
  color:#f9f7f7;
  font-size: 1.2rem;
  
}

.chatHeader img {
  width: 10%;
  border-radius: 50%;
  padding: 5px;
  
}

.recentMessages {
  border: solid #112d4e 4px;
  width: 100%;
  /* height: 100%; */
  height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: start;
  border-radius: 0 0 15px 15px;
  background-color: white;
  padding: 10px;

  /* background-color: lightblue; */
  overflow-y: scroll;
}

.chatForm {
  display: flex;
  width: 60%;
  max-height: 180px;
  justify-content: center;
  align-items: center;
  background-color: #f9f7f7;
  border-radius: 20px;
  border: solid 5px #112d4e;
  margin-top: 20px;
  box-shadow: 0px 0px 5px #dbe2ef;
}

textarea {
  height: 70%;
  /* border: solid 4px rgb(22, 0, 102); */
  border: none;
  border-radius: 15px;
  background-color: white;
  resize: none;
  width: 60%;
  padding: 10px;
  
}

textarea:disabled {
  background-color: #dbe2ef;
}

.chatForm button {
  width: 25%;
  margin-left: 20px;
  
  /* border-radius: 30px; */
}

.submitButton {
  background-color: #ff4343;
  color: #f9f7f7;
  align-self: center;
}

.sent, .recieved {
  padding: 10px 20px;
  border-radius: 20px;
  max-width: 70%;
  
  position: relative;
  z-index: 0;
  margin-bottom: 20px;
}

.sent {
align-self: end;
background-color: rgb(177, 231, 177);

}

.recieved {
  align-self: start;
  background-color: rgb(141, 174, 218);
 
}

.sent:after, .recieved:after{
  content: "";
  position: absolute;
  border-style: solid;
  border-width: 18px 18px 0px 18px;
  top: 98%;
  bottom: -18px;
  z-index: -1;
}

.sent:after {
  border-color: rgb(177, 231, 177) transparent transparent transparent;
  right: 25px;
}

.recieved:after {
  border-color: rgb(141, 174, 218) transparent transparent transparent;
  left: 25px;
}

.time {
  color: rgb(63, 63, 63);
  font-size: 0.8rem;
}

/* Settings page */

.settingsPage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #dbe2ef;
  padding: 30px;
  border: solid 5px #112d4e;
  animation: appear both 0.5s 1;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}

.settingsPage form:last-of-type {
    display: flex;
    flex-direction: column;
}

.close{
  position: absolute;
  top: 5px;
  right: 10px;

}


/* Alert message */

.alert {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ff4343;
  color: #f9f7f7;
  display: flex;
  flex-direction: column;
  padding: 50px 30px;
  border-radius: 20px;
  z-index: 5;
  border: solid 5px #112d4e;
  min-height: 40vh;
}

.alert div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.alert div button {
  background-color: #f9f7f7;
  color: #112d4e;
  margin-top: 40px;
  

}

footer {
  text-align: center;
  /* background-color: rgb(27, 27, 27); */
  /* color: #4f5165; */
  background-color: #dbe2ef;
  border-top: solid 1px #112d4e;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5vh;
}


/* Keyframes */

@keyframes appear {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
